@import url('https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css');
@import url('https://fonts.googleapis.com/css?family=Girassol&display=swap');
@import url('https://fonts.googleapis.com/css?family=Cinzel:700|Quattrocento&display=swap');

*{
  padding: 0;
  margin: 0;
}

.changeBackground{
  background-image:linear-gradient(0deg, #002e77 0%, #0390d1 50%);
}

.caixa{
  /* background-image: linear-gradient(0deg, #002e77 0%, #0390d1 100%); */
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: 'Quattrocento', serif;
}

.container{
  padding: 2%;
  /* background-color: #002e77; */
  font-family: 'Quattrocento', serif;
  color: white;
  background-color: white;
  margin-top: 2%;
  box-shadow: 11px 9px 20px 0px #135675;
  border-radius: 12px;
}

.agua{
  height: 200px;
}

.title{
  height: 200px;
  text-align: center;
  font-family: Girassol, 'Times New Roman', Times, serif;
}
.title h1{
    font-size: 4.5rem;
}

.language{
  color: black;
}
.container-fluid{
  padding-left: 0;
  padding-right: 0;
}

@media only screen and (max-width: 1024px){
  .title h1{
    font-size: large;
  }
  p, a{
    font-size: large;
  }
  h3{
    font-size: x-large;
  }
}